<template>
  <div id="statistics">
    <div class="top">
      <div class="btn">今日访客：{{ countData.todayVisitor }}人</div>
      <div class="btn">累计访客：{{ countData.sumVisitor }}人</div>
      <div class="btn">今日收入：{{ countData.todayGet }}元</div>
      <div class="btn">累计收入：{{ countData.sumGet }}元</div>
    </div>
    <div id="pic_visitor"></div>
    <div id="pic_income"></div>
    <div class="line"></div>
    <div class="order">
      <div class="title">订单统计</div>
      <div class="orderData">
        <div class="btn left">待处理订单：{{ order.todo }}</div>
        <div class="btn">已完成订单：{{ order.done }}</div>
      </div>
    </div>
    <div class="line"></div>
    <div class="sales">
      <div class="title">销量统计</div>
      <el-table
          class="salesTable"
          :data="salesData"
          show-summary
          :summary-method="getSummaries">
        <el-table-column
            prop="productName"
            label="商品名称"
            mid-width="20%"
            align="center">
        </el-table-column>
        <el-table-column
            prop="productPrice"
            label="商品单价"
            mid-width="20%"
            align="center">
        </el-table-column>
        <el-table-column
            prop="today"
            label="今日售出"
            mid-width="20%"
            align="center">
          <template slot-scope="scope">
            {{ scope.row.today }}件
          </template>
        </el-table-column>
        <el-table-column
            prop="month"
            label="本月售出数量"
            mid-width="20%"
            align="center">
          <template slot-scope="scope">
            {{ scope.row.month }}件
          </template>
        </el-table-column>
        <el-table-column
            prop="total"
            label="累计售出"
            mid-width="20%"
            align="center">
          <template slot-scope="scope">
            {{ scope.row.total }}件
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
  //init echarts的重点，引入echarts
  var echarts = require('echarts');

  export default {
    name: "Statistics",
    data() {
      return {
        countData: {
          todayVisitor: '',
          sumVisitor: '',
          todayGet: '',
          sumGet: ''
        },
        salesData: [],
        order: {
          todo: '',
          done: ''
        },
        x1: [],
        y1: [],
        x2: [],
        y2: []
      }
    },
    methods: {
      getData() {
        //获取首页的四个数据
        this.getTodayPeople();
        this.getAllPeople();
        this.getTodayCount();
        this.getAllCount();
        //获取图表
        this.getData1();
        this.getData2();
        //获取待处理/已完成订单数量
        this.getTodoOrder();
        this.getDoneOrder();
        //获取销量统计的数据
        this.getSalesTable();
        /*this.getSalesTotal();*/
      },
      getTodayPeople() {
        this.$axios.get('/count/today',).then((res) => {
          if(res.status === 200) {
            this.countData.todayVisitor = res.data;
          }
          else {
            this.$message.error(res.msg);
          }
        }).catch((err) => {
          console.log(err);
        })
      },
      getAllPeople() {
        this.$axios.get('/count/all',).then((res) => {
          if(res.status === 200) {
            this.countData.sumVisitor = res.data;
          }
          else {
            this.$message.error(res.msg);
          }
        }).catch((err) => {
          console.log(err);
        })
      },
      getTodayCount() {
        this.$axios.get('/orders/income',).then((res) => {
          if(res.status === 200) {
            this.countData.todayGet = res.data / 100;
          }
          else {
            this.$message.error(res.msg);
          }
        }).catch((err) => {
          console.log(err);
        })
      },
      getAllCount() {
        this.$axios.get('/orders/incomeMonth',).then((res) => {
          if(res.status === 200) {
            this.countData.sumGet = res.data / 100;
          }
          else {
            this.$message.error(res.msg);
          }
        }).catch((err) => {
          console.log(err);
        })
      },
      getData1() {
        this.$axios.get('/count/allCount',).then((res) => {
          if(res.status === 200) {
            let data = res.data;
            //获取时间数组
            let dateStr = data.map(function(obj){
              return obj.date;
            }).join(",");
            this.x1 = dateStr.split(",");
            /*this.x1 = this.x1.slice(this.x1.length - 7, this.x1.length);*/
            //获取收入数组
            let countStr = data.map(function(obj){
              return obj.count;
            }).join(",");
            this.y1 = countStr.split(",").map(Number);
            //定义series
            let seriesList = [{
              data: [],
              type: 'line',
              label: {
                show: true
              }
            }]
            seriesList[0].data = this.y1;
            this.drawLine1(seriesList,this.x1);
          }
          else {
            this.$message.error(res.msg);
          }
        }).catch((err) => {
          console.log(err);
        })
      },
      getData2() {
        this.$axios.get('/orders/all',).then((res) => {
          if(res.status === 200) {
            let data = res.data;
            //获取时间数组
            let dateStr = data.map(function(obj){
              return obj.date;
            }).join(",");
            this.x2 = dateStr.split(",")
            //获取收入数组
            let incomeStr = data.map(function(obj){
              return obj.income;
            }).join(",");
            this.y2 = incomeStr.split(",").map(Number);
            for(let i = 0; i < this.y2.length; i++) {
              this.y2[i] = this.y2[i]/100;
            }
            //定义series
            let seriesList = [{
              data: [],
              type: 'line',
              label: {
                show: true
              }
            }]
            seriesList[0].data = this.y2;
            this.drawLine2(seriesList,this.x2);
          }
          else {
            this.$message.error(res.msg);
          }
        }).catch((err) => {
          console.log(err);
        })
      },
      drawLine1(seriesList,x) {
        //inti echarts
        var myChart = echarts.init(document.getElementById('pic_visitor'));
        myChart.setOption({
          /*title: {
            text: 'ECharts 入门示例'
          },*/
          tooltip: {},
          xAxis: {
            name: '日期',
            data: x,
            type: 'category',
            axisLine: {
              lineStyle: {
                // 设置x轴颜色
                color: '#BFBFBF'
              }
            }
          },
          yAxis: {
            name: '访客数量',
            type: 'value',
            /*splitNumber: 4,  // 设置y轴刻度间隔个数*/
            axisLine: {
              lineStyle: {
                // 设置y轴颜色
                color: '#BFBFBF'
              }
            }
          },
          series: seriesList
          /*series: [{
            data: [800, 230, 224, 218, 135, 147, 260],
            type: 'line',
            /!*itemStyle: {
              normal: {
                // 拐点上显示数值
                label: {
                  show: true
                }
              }
            }*!/
            label: {
              show: true
            }
          }]*/
        });
      },
      drawLine2(seriesList,x) {
        //inti echarts
        var myChart = echarts.init(document.getElementById('pic_income'));
        myChart.setOption({
          xAxis: {
            name: '日期',
            data: x,
            type: 'category',
            axisLine: {
              lineStyle: {
                // 设置x轴颜色
                color: '#BFBFBF'
              }
            }
          },
          yAxis: {
            name: '收入',
            type: 'value',
            /*splitNumber: 4,  // 设置y轴刻度间隔个数*/
            axisLine: {
              lineStyle: {
                // 设置y轴颜色
                color: '#BFBFBF'
              }
            }
          },
          series: seriesList
        });
      },
      getTodoOrder() {
        this.$axios.get('/orders/waiting',).then((res) => {
          if(res.status === 200) {
            this.order.todo = res.data;
          }
          else {
            this.$message.error(res.msg);
          }
        }).catch((err) => {
          console.log(err);
        })
      },
      getDoneOrder() {
        this.$axios.get('/orders/finished',).then((res) => {
          if(res.status === 200) {
            this.order.done = res.data;
          }
          else {
            this.$message.error(res.msg);
          }
        }).catch((err) => {
          console.log(err);
        })
      },
      getSalesTable() {
        this.$axios.get('/orders/sales',).then((res) => {
          if(res.status === 200) {
            this.salesData = res.data;
            for(let i = 0; i < this.salesData.length; i++) {
              this.salesData[i].productPrice = this.salesData[i].productPrice / 100;
            }
          }
          else {
            this.$message.error(res.msg);
          }
        }).catch((err) => {
          console.log(err);
        })
      },
      /*getSalesTotal() {
        this.$axios.get('/orders/total',).then((res) => {
          console.log(res);
          /!*if(res.status === 200) {
            this.tableData = res.data;
          }
          else {
            this.$message.error(res.msg);
          }*!/
        }).catch((err) => {
          console.log(err);
        })
      },*/
      getSummaries(param) {
        const { columns, data } = param;
        const sums = [];
        columns.forEach((column, index) => {
          if (index === 0) {
            sums[index] = '';
            return;
          }
          if (index === 1) {
            sums[index] = '合计';
            return;
          }
          const values = data.map(item => Number(item[column.property]));
          if (!values.every(value => isNaN(value))) {
            sums[index] = values.reduce((prev, curr) => {
              const value = Number(curr);
              if (!isNaN(value)) {
                return prev + curr;
              } else {
                return prev;
              }
            }, 0);
            sums[index] += '件';
          } else {
            sums[index] = 'N/A';
          }
        });
        return sums;
      }
    },
    beforeMount() {
      this.getData();
    }
  }
</script>

<style lang="less" scoped>
  @import "../../assets/style/statistics/statistics";
</style>
